.post-header {
 margin-bottom: $vertical-spacing-unit;
}
.post-list {
 margin: 0;
 padding: 0;
 list-style: none;

 > li {
 border-bottom: 1px solid $lighter-gray;
 padding-bottom: $space-2;
 padding-top: $space-2;
 }
}
.post-title {
 font-size: 38px;
 letter-spacing: -1px;
 line-height: 0.6;
 margin-bottom: 10px;
 margin-top: 0;
 // @include media-query($on-laptop) {
 // font-size: 36px;
 // }
}
.post-content {
 h2 {
 font-size: 40px;
 // @include media-query($on-laptop) {
 // font-size: 28px;
 // }
 }
 h3 {
 font-size: 30px;
 // @include media-query($on-laptop) {
 // font-size: 22px;
 // }
 }
 h4 {
 font-size: 20px;
 // @include media-query($on-laptop) {
 // font-size: 18px;
 // }
 }
}
.post-meta {
 color: $grey-color;
 font-size: $small-font-size;
 margin-bottom: 0px;
}
.post-link {
 display: block;
 font-size: 42px;
}
